(function() {
    $('.menu_heading').on('click', function() {
        $(this).toggleClass('active');
    });

    $('.menu_heading').on('mouseenter', function() {
        if (window.innerWidth >= 768 && !$(this).hasClass('active')) {
            $(this).addClass('active');
        }
    });

    $('.menu_content').on('mouseleave', function() {
        if (window.innerWidth >= 768) {
            $('.menu_heading').removeClass('active');
        }
    });

    $('.docs-heading, .qa-heading').on('click', function() {
        $(this).next().fadeToggle();
        $(this).toggleClass('active');
    });

    $('.menu_content > a').on('click', function() {
        var anchor = $(this).attr('data-href');
        $('html, body').animate({
            scrollTop: $("#" + anchor).offset().top
        }, 1000);
        $('.menu_heading').toggleClass('active');
    });
})();